}

.scroller {
	display: flex;
	flex-direction: column;
	align-items: center;

	&__text {
		margin-bottom: 16px;
	}

	&__icon {
		animation: scroller-pulse 1.2s infinite ease-in-out 3.25s;
	}

}


.tile {
	height : 100%;
	min-height: 60px;
	position: relative;
	overflow: hidden;
	aspect-ratio: 1;
	
	&__back, .back {
		width: 100%;
		height: 100%;
		position: absolute;
		inset: 0;

		&__inner {
			width: 100%;
			height: 100%;
		}

		&__image {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

	}
	
	&__front, .front {
		width: 100%;
		height: 100%;
		position: absolute;
		transition: all 0.6s ease;
		inset: 100% 0 0 0;
		background-color: $white;
		
		&--white { background-color: $white; }
		&--black { background-color: $black; }

		&__inner {
			padding: 24px;
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
		}

		&__title {
			margin-top: 48px;
			margin-bottom: 8px;
		}

		&__link {
			align-self: flex-end;
			margin-top: auto;
			display: block;
		}

		&__title, 
		&__subtitle, 
		&__link {
			opacity: 0;
			position: relative;
			top: 16px;
			transition: all 0.6s ease;
		}
		
	}

	&:hover {
		.tile__front { inset: 0;  }
		.front__title, 
		.front__subtitle, 
		.front__link {
			opacity: 1;
			top: 0px;
			transition-delay: 0.3s;
		}	
		
	}

}
.teaser {

	&__heading {
		margin-bottom: 24px;
		max-width: 440px;
	}

	&__line {
		margin-bottom: 32px;
	}

	&__text {
		margin-bottom: 48px;
		max-width: 880px;
	}

}
.navigation-desktop {
	width: 100%;
	box-shadow: $box-shadow;
	position: sticky;
	inset: 0 0 unset 0;

	&__inner {
		display: flex;
		flex-direction: row;
		gap: 40px;
		align-items: center;
		padding-top: 16px;
		padding-bottom: 16px;
	}

	&__logo {
		margin-left: auto;
		width: 160px;
	}

	&__link {
		line-height: 1;
		color: $white;

		&--active {
			color: $orange;
		}

	}

	@media (max-width: 1050px) {

		&__inner {
			gap: 24px;
		}

	}

	@media (max-width: 900px) {
			display: none;
	}

}
.navigation-mobile {
	box-shadow: $box-shadow;
	display: none;
	position: relative;

	&__inner {
		background-color: $black;
	}
	
	&__bar {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		width: 100%;
		height: 50px;
		z-index: 5;
		background-color: $black;
	}

	&__logo {
		width: 120px;
	}

	&__hamburger {
		width: 40px;
	}

	&__panel {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 16px;
		height: calc(100vh - 50px);
		position: absolute;
		top: -100vh;
		left: 0;
		right: 0;
		width: 100%;
		z-index: -5;
		background-color: $orange;

		&--active {
			top: 50px;
		}

	}
	
	&__link {
		padding: 8px;
		color: $white;

		&--active {
			color: $orange;
		}

	}

	@media (max-width: 900px) {
		display: block;
	}
	
	@media (max-height: 500px) {
	
		&__panel {
			gap: 8px;
		}
			
	}

}
.project-tile-set {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-auto-rows: 1fr;
	
	@media (max-width: 900px) {
		grid-template-columns: repeat(2, 1fr);
	}
	
	@media (max-width: 600px) {
		grid-template-columns: repeat(1, 1fr);
	}

}